@import "../../index.scss";
.singleCharacter {
    &__wrapper {
        display: flex;
        column-gap: 50px;
        margin-top: 50px;
    }
    &__img {
        max-width: 293px;
        min-height: 293px;
        object-fit: cover;
    }
    &__content {
        &-title-first {
            @include fz700-22;
            color: $dark;
        }
        &-text {
            margin-top: 28px;
            @include fz400-18;
            color: $dark;
            max-width: 550px;
        }
    }
}
